.app-logo {
  height: 7vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .app-logo {
    animation: app-logo-spin infinite 20s linear;
  }
}

@keyframes app-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.header {
  display: flex;
  width: 100vw;
  height: 10vh;
}


.markdown-previewer {
  width: 95vw;
  display: flex;
  margin: auto;
}


.editor-header {
  height: 5vh;
}

#editor {
  font-size: 1.35rem;
  resize: none;
  width: 100%;
  height: 80vh;
  overflow: auto;
  background: none;
  color: beige;
  border: 0.4rem solid beige;
  padding: 1.5rem;
}

#editor:focus {
  outline: none;
}


.gutter:hover {
  background-color: rgb(248, 248, 248);
  cursor: col-resize;
}

.gutter {
  align-self: flex-end;
  height: 80vh;
  width: 0.6rem !important;
  background-color: beige;
}


.preview-header {
  height: 5vh;
}

.preview-field {
  font-size: 1.6rem;
  overflow: auto;
  padding: 2rem 2rem 2rem 2.5rem;
  height: 80vh;
  border: 0.4rem solid beige;
}

.preview-field h1 {
  font-size: 3rem;
}

.preview-field h2 {
  font-size: 2rem;
}

#preview a {
  color: rgb(32, 93, 91);
}

#preview img {
  display: block;
  max-width: 90%;
}


.top-button {
  position: fixed;
  bottom: 2%;
  right: 6%;
  font-weight: 700;
  color: beige;
  text-decoration: none;
  background-color: rgba(90, 108, 123, 0.445);
  border-radius: 7%;
  padding: 0.3rem 0.6rem;
}

@keyframes inAnimation {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  
  100% {
    opacity: 1;
    visibility: visible;
  }
}

@keyframes outAnimation {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    visibility: hidden;
  }
}


@media (max-width: 42.5em) {
  .markdown-previewer {
    flex-direction: column;
  }
  #editor {
    height: 40vh;
  }

  .app-logo {
    height: 6vh;
  }

  .preview-field {
    height: 60vh;
  }

  .header {
    height: 8vh;
  }
}

@media (max-width: 26.25em) {
  .preview-field h1 {
    font-size: 2.8rem;
  }

  .preview-field h2 {
    font-size: 1.8rem;
  }

  .header h1 {
    position: relative;
    top: 0;
    right: 0;
    padding-right: 2.2rem;
    width: 100vw;
    text-align: right;
  }
}