

*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body {
  font-size: 1.8rem;
  font-family: 'Segoe UI', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(53, 60, 56);
  color: beige;
}

h1 {
  font-size: 4.5rem;
}

h2 {
  font-size: 3rem;
}

pre {
  border: 0.2rem rgba(0, 0, 0, 0.481) solid;
  background-color: rgba(104, 126, 113, 0.212);
  overflow: auto;
}

pre code {
  background-color: transparent;
}

code {
  background-color: rgba(123, 123, 123, 0.218);
  font-family: 'Source Code Pro', Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media (max-width: 42.5em) {
  h1 {
    font-size: 3.5rem;
  }
  h2 {
    font-size: 2.5rem;
  }
}

@media (max-width: 26.25em) {
  h1 {
    font-size: 3rem;
  }
  h2 {
    font-size: 2rem;
  }
}